<template>
  <div class="gift-box">
    <div class="bg"></div>
    <header>
      <div class="btn" @click="singUp"></div>
    </header>
    <section class="section1"></section>
    <section class="section2"></section>
    <section class="section3"></section>
    <section class="section4"></section>
    <section class="section5"></section>
    <footer>
      <div class="btn" @click="singUp">
        <img
          src="@/assets/giftBox/second/footer_btn_text.png"
          alt=""
          class="pulsate-bck">
      </div>
    </footer>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { clickLog } from '@/helper/clickLog'
export default {
  components: {

  },
  data () {
    return {}
  },
  computed: {
    ...mapState('giftBox', ['productSn'])
  },
  created () {
    this.checkIsBought()
    this.$store.dispatch('giftBox/setLink', location.href)
  },
  methods: {
    /**
     * @description 立即支付
     */
    singUp () {
      clickLog({
        module_ori: '报名按钮',
        module_part: 'xby_lhtf_join_click'
      })
      localStorage.removeItem('giftBoxPayRetain')// 清除 挽留弹窗
      this.$router.push({
        path: '/GiftBoxPay',
        query: {
          ...this.$route.query
        }
      })
    },
    /**
     * @description 查询是否购买
     */
    async checkIsBought () {
      try {
        const phone = localStorage.getItem('giftBoxPhone')// 若用户已经购买过  则会存在localStorage
        if (!phone || !(/^[1]([3-9])[0-9]{9}$/.test(phone))) return
        await this.$apis.giftBox.checkOrder({
          phone: phone,
          product_sn: this.productSn
        })
        // 1000 留在当前页面  继续完成支付  不做任何操作
      } catch (err) {
        console.log(err)
        // 30001 学习过同类课程
        // 30002 学习过此课程
        // 30003 学习过此课程
        // 30004 学习过此课程
        // 33001 手机号未填写或未授权
        const { code, data: { order_num: orderNum } } = err
        localStorage.setItem('giftBoxOrderNum', orderNum)// 存储order_num  在地址页面会用到
        if (code === 30006) {
          this.$router.push('/GiftBoxPaySuccess')
        }
      }
    }
  }
}
</script>

